<template>
  <div class="search-view">
    <h2>Sermon Search</h2>
    
    <div class="search-box">
      <input 
        v-model="searchQuery" 
        @input="handleSearch"
        type="text" 
        placeholder="Search sermons by title or transcript content..."
        class="search-input"
        :disabled="!db"
      >
    </div>

    <div v-if="loading" class="loading">
      Searching...
    </div>

    <div v-else-if="results.length > 0" class="results">
      <div class="results-summary">
        <h3>Found {{ results.length }} matches</h3>
        <div class="stats">
          <span>Title Matches: {{ titleMatches }}</span>
          <span>Transcript Matches: {{ transcriptMatches }}</span>
        </div>
      </div>

      <div class="results-tabs">
        <button 
          :class="['result-tab', { active: resultTab === 'all' }]"
          @click="resultTab = 'all'"
        >
          All Results ({{ results.length }})
        </button>
        <button 
          :class="['result-tab', { active: resultTab === 'titles' }]"
          @click="resultTab = 'titles'"
        >
          Title Matches ({{ titleMatches }})
        </button>
        <button 
          :class="['result-tab', { active: resultTab === 'transcripts' }]"
          @click="resultTab = 'transcripts'"
        >
          Transcript Matches ({{ transcriptMatches }})
        </button>
      </div>

      <div class="results-table">
        <table>
          <thead>
            <tr>
              <th>Type</th>
              <th>Date</th>
              <th>Speaker</th>
              <th>Video Title</th>
              <th>Time</th>
              <th>Match</th>
              <th>Watch</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(result, index) in filteredResults" :key="index">
              <td>
                <span :class="result.type === 'Title' ? 'badge-title' : 'badge-transcript'">
                  {{ result.type }}
                </span>
              </td>
              <td>{{ result.date }}</td>
              <td>{{ result.speaker }}</td>
              <td class="title-cell">{{ result.title }}</td>
              <td>{{ result.timestamp }}</td>
              <td class="match-text">{{ result.match }}</td>
              <td>
                <a :href="result.url" target="_blank" class="watch-link" title="Watch video">‚ñ∂</a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div v-else-if="searchQuery.length >= 2" class="no-results">
      No matches found for "{{ searchQuery }}"
    </div>

    <div v-else-if="db" class="instructions">
      <p>üîç Enter at least 2 characters to search</p>
      <p class="hint">Try searching for topics like "faith", "prayer", "salvation", or Bible references</p>
    </div>
  </div>
</template>


<script>
export default {
  name: 'SearchView',
  props: {
    db: {
      type: Object,
      required: true
    },
    speakers: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      searchQuery: '',
      results: [],
      loading: false,
      resultTab: 'all',
      searchTimeout: null
    }
  },
  computed: {
    titleMatches() {
      return this.results.filter(r => r.type === 'Title').length
    },
    transcriptMatches() {
      return this.results.filter(r => r.type === 'Transcript').length
    },
    filteredResults() {
      if (this.resultTab === 'titles') {
        return this.results.filter(r => r.type === 'Title')
      } else if (this.resultTab === 'transcripts') {
        return this.results.filter(r => r.type === 'Transcript')
      }
      return this.results
    }
  },
  methods: {
    handleSearch() {
      if (this.searchQuery.length < 2) {
        this.results = []
        return
      }
      
      this.loading = true
      
      clearTimeout(this.searchTimeout)
      this.searchTimeout = setTimeout(() => {
        this.performSearch()
        this.loading = false
      }, 300)
    },
    
    performSearch() {
      if (!this.db) {
        console.error('Database not ready')
        return
      }
      
      console.log(`Searching for: "${this.searchQuery}"`)
      const results = []
      const query = this.searchQuery.trim()
      
      try {
        // Search video titles
        const titleQuery = `
          SELECT video_id, title, date_published, url
          FROM videos
          WHERE title LIKE ?
          LIMIT 50
        `
        
        const titleResults = this.db.exec(titleQuery, [`%${query}%`])
        
        if (titleResults.length > 0) {
          const rows = titleResults[0].values
          rows.forEach(row => {
            const [videoId, title, date, url] = row
            results.push({
              type: 'Title',
              date: date.substring(0, 10),
              speaker: this.speakers[videoId] || 'Unknown',
              title: title,
              timestamp: '00:00:00',
              match: `Title contains: "${query}"`,
              url: url,
              score: 10
            })
          })
        }
        
        console.log(`Found ${results.length} title matches`)
        
        // Search transcripts - use regular LIKE instead of FTS5
        const transcriptQuery = `
          SELECT ts.video_id, v.title, v.date_published, ts.start_time, ts.text, ts.vimeo_url
          FROM transcript_segments ts
          JOIN videos v ON ts.video_id = v.video_id
          WHERE ts.text LIKE ?
          ORDER BY ts.start_time
          LIMIT 100
        `
        
        const transcriptResults = this.db.exec(transcriptQuery, [`%${query}%`])
        
        if (transcriptResults.length > 0) {
          const rows = transcriptResults[0].values
          rows.forEach(row => {
            const [videoId, title, date, startTime, text, url] = row
            results.push({
              type: 'Transcript',
              date: date.substring(0, 10),
              speaker: this.speakers[videoId] || 'Unknown',
              title: title,
              timestamp: this.formatTimestamp(startTime),
              match: text.substring(0, 200) + (text.length > 200 ? '...' : ''),
              url: url,
              score: 1
            })
          })
        }
        
        console.log(`Found ${transcriptResults.length > 0 ? transcriptResults[0].values.length : 0} transcript matches`)
        
        this.results = results
        console.log(`Total results: ${results.length}`)
        
      } catch (error) {
        console.error('Search error:', error)
        alert(`Search failed: ${error.message}`)
      }
    },

    
    formatTimestamp(seconds) {
      const hours = Math.floor(seconds / 3600)
      const minutes = Math.floor((seconds % 3600) / 60)
      const secs = Math.floor(seconds % 60)
      return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
    }
  }
}
</script>


<style scoped>
.search-view {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

h2 {
  margin-bottom: 1.5rem;
  color: #31333F;
  font-size: 2rem;
  font-weight: 600;
}

.search-box {
  margin-bottom: 1.5rem;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-family: "Source Sans Pro", sans-serif;
  border: 1px solid #d3d8e0;
  border-radius: 0.25rem;
  transition: border-color 0.2s;
  background-color: #ffffff;
}

.search-input:focus {
  outline: none;
  border-color: #ff2b2b;
  box-shadow: 0 0 0 1px #ff2b2b;
}

.search-input:disabled {
  background: #f0f2f6;
  cursor: not-allowed;
}

.loading {
  text-align: center;
  padding: 40px 20px;
  color: #31333F;
  font-size: 1rem;
}

.results-summary {
  background: #d4edda;
  padding: 1rem 1.25rem;
  border-radius: 0.25rem;
  margin-bottom: 1.5rem;
  border-left: 4px solid #28a745;
}

.results-summary h3 {
  color: #155724;
  margin-bottom: 0.75rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.stats {
  display: flex;
  gap: 2rem;
  color: #155724;
  font-size: 0.95rem;
}

.stats span {
  font-weight: 400;
}

.results-tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.result-tab {
  padding: 0.5rem 1rem;
  background: #f0f2f6;
  border: 1px solid #d3d8e0;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 0.95rem;
  font-family: "Source Sans Pro", sans-serif;
  color: #31333F;
}

.result-tab:hover {
  background: #e6e9ef;
  border-color: #cbd5e0;
}

.result-tab.active {
  background: #0068c9;
  color: white;
  border-color: #0068c9;
  font-weight: 500;
}

.results-table {
  overflow-x: auto;
  background: white;
  border: 1px solid #e6e9ef;
  border-radius: 0.25rem;
}

table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.95rem;
}

thead {
  background: #f0f2f6;
  position: sticky;
  top: 0;
  z-index: 10;
}

th {
  padding: 0.75rem;
  text-align: left;
  font-weight: 600;
  color: #31333F;
  border-bottom: 2px solid #e6e9ef;
  white-space: nowrap;
  font-size: 0.875rem;
}

td {
  padding: 0.75rem;
  border-bottom: 1px solid #f0f2f6;
  vertical-align: top;
  color: #31333F;
}

tr:hover {
  background: #fafbfc;
}

.badge-title {
  background: #0068c9;
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  font-size: 0.8rem;
  font-weight: 600;
}

.badge-transcript {
  background: #28a745;
  color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  font-size: 0.8rem;
  font-weight: 600;
}

.title-cell {
  max-width: 400px;
  font-weight: 500;
  color: #31333F;
}

.match-text {
  max-width: 500px;
  line-height: 1.5;
  color: #31333F;
  font-size: 0.9rem;
}

.watch-link {
  color: #0068c9;
  text-decoration: none;
  font-size: 1.2rem;
  transition: all 0.2s;
  display: inline-block;
}

.watch-link:hover {
  color: #ff2b2b;
  transform: scale(1.15);
}

.no-results {
  text-align: center;
  padding: 60px 20px;
  color: #31333F;
  font-size: 1rem;
}

.no-results::before {
  content: 'üîç';
  display: block;
  font-size: 3rem;
  margin-bottom: 20px;
  opacity: 0.5;
}

.instructions {
  text-align: center;
  padding: 60px 20px;
  color: #31333F;
}

.instructions p {
  margin: 10px 0;
  font-size: 1.05rem;
}

.instructions .hint {
  color: #808495;
  font-size: 0.95rem;
  font-style: normal;
}

/* Mobile responsive */
@media (max-width: 768px) {
  .search-view {
    padding: 0 0.5rem;
  }
  
  .results-table {
    font-size: 0.85rem;
  }
  
  th, td {
    padding: 0.5rem;
  }
  
  .match-text {
    max-width: 200px;
  }
  
  .stats {
    flex-direction: column;
    gap: 0.5rem;
  }
}
</style>

