<template>
  <div id="app">
    <header>
      <img src="./assets/download.png" alt="HHBC Sermon Search" class="logo" v-if="logoExists">
      <h1 v-else>HHBC Sermon Search</h1>
    </header>

    <!-- Show loading overlay if database is loading -->
    <div v-if="loadingDb" class="global-loading">
      <div class="loading-spinner"></div>
      <p>Loading database... {{ loadingProgress }}</p>
      <p class="loading-hint">This may take a minute on first load (142MB compressed)</p>
    </div>

    <div v-else>
      <nav class="tabs">
        <button 
          v-for="tab in tabs" 
          :key="tab.id"
          :class="['tab', { active: currentTab === tab.id }]"
          @click="currentTab = tab.id"
        >
          {{ tab.name }}
        </button>
      </nav>

      <main>
        <SearchView v-if="currentTab === 'search'" :db="db" :speakers="speakers" />
        <VideoListView v-if="currentTab === 'videos'" :db="db" :speakers="speakers" />
        <BibleHeatMapView v-if="currentTab === 'bible'" :db="db" :speakers="speakers" />
      </main>
    </div>
  </div>
</template>

<script>
import SearchView from './views/SearchView.vue'
import VideoListView from './views/VideoListView.vue'
import BibleHeatMapView from './views/BibleHeatMapView.vue'
import initSqlJs from 'sql.js' // eslint-disable-line
import pako from 'pako' // eslint-disable-line

export default {
  name: 'App',
  components: {
    SearchView,
    VideoListView,
    BibleHeatMapView
  },
  data() {
    return {
      currentTab: 'search',
      logoExists: false,
      tabs: [
        { id: 'search', name: 'Home' },
        { id: 'videos', name: 'Video List' },
        { id: 'bible', name: 'Bible Heat Map' }
      ],
      loadingDb: true,
      loadingProgress: '',
      db: null,
      speakers: {}
    }
  },
  async mounted() {
    this.checkLogo()
    await this.loadDatabase()
  },
  beforeUnmount() {
    if (this.db) {
      this.db.close()
    }
  },
  methods: {
    checkLogo() {
      const img = new Image()
      img.onload = () => { this.logoExists = true }
      img.onerror = () => { this.logoExists = false }
      img.src = require('./assets/download.png')
    },
    
    async loadDatabase() {
      try {
        this.loadingProgress = 'Initializing SQL.js...'
        
        const SQL = await initSqlJs({
          locateFile: file => `https://sql.js.org/dist/${file}`
        })
        
        this.loadingProgress = 'Downloading compressed database (142MB)...'
        console.log('Downloading compressed database...')
        
        const basePath = process.env.NODE_ENV === 'production' ? '/hhbc-sermon-search/' : '/'
        const response = await fetch(`${basePath}data/transcripts.db.gz`)

        if (!response.ok) {
          throw new Error(`Failed to load database: ${response.status}`)
        }
        
        const contentLength = response.headers.get('content-length')
        const total = parseInt(contentLength, 10)
        let loaded = 0
        
        const reader = response.body.getReader()
        const chunks = []
        
        let done = false
        while (!done) {
          const result = await reader.read()
          done = result.done
          
          if (result.value) {
            chunks.push(result.value)
            loaded += result.value.length
            
            if (total) {
              const percent = Math.round((loaded / total) * 100)
              this.loadingProgress = `Downloading: ${percent}% (${Math.round(loaded/1024/1024)}MB / ${Math.round(total/1024/1024)}MB)`
            }
          }
        }
        
        this.loadingProgress = 'Decompressing database...'
        console.log('Decompressing database...')
        
        const compressedData = new Uint8Array(loaded)
        let position = 0
        for (const chunk of chunks) {
          compressedData.set(chunk, position)
          position += chunk.length
        }
        
        const decompressedData = pako.ungzip(compressedData)
        console.log(`Decompressed to ${decompressedData.length / 1024 / 1024} MB`)
        
        this.loadingProgress = 'Loading database into memory...'
        
        this.db = new SQL.Database(decompressedData)
        console.log('‚úÖ Database loaded successfully')
        
        await this.loadSpeakers()
        
        this.loadingDb = false
        
      } catch (error) {
        console.error('Error loading database:', error)
        this.loadingProgress = `Error: ${error.message}`
        alert(`Failed to load database: ${error.message}`)
      }
    },
    
    async loadSpeakers() {
      try {
        const basePath = process.env.NODE_ENV === 'production' ? '/hhbc-sermon-search/' : '/'
        const response = await fetch(`${basePath}data/speakers.json`)
        if (response.ok) {
          this.speakers = await response.json()
          console.log(`‚úÖ Loaded ${Object.keys(this.speakers).length} speaker mappings`)
        }
      } catch (error) {
        console.warn('Could not load speakers.json:', error)
      }
    }
  }
}
</script>

